import React from "react";
import axios from "axios";

import "./App.css";
import "bootstrap/dist/css/bootstrap.min.css";

import ListGroup from "react-bootstrap/ListGroup";

import Todo from "./Todo";
import AddTodo from "./AddTodo";
import TodoSettings from "./TodoSettings";

class App extends React.Component {
  constructor() {
    super();
    this.state = {
      showCompleted: true,
      todos: []
    };
  }

  componentDidMount() {
    this.onReadTodos();
  }

  onReadTodos = () => {
    axios.get("https://boiling-woodland-05459.herokuapp.com/api/").then(res => {
      this.setState(prevState => {
        return {
          ...prevState,
          todos: res.data
        };
      });
    });
  }

  onChangeTodoCompleted = todo => {
    const newTodo = {
      id: todo.id,
      title: todo.title,
      isCompleted: !todo.isCompleted
    };

    axios
      .put(
        `https://boiling-woodland-05459.herokuapp.com/api/${newTodo.id}`,
        newTodo
      )
      .then(res => {
        this.setState(prevState => {
          return {
            ...prevState,
            todos: prevState.todos.map(item => {
              if (item === todo) return newTodo;
              else return item;
            })
          };
        });
      });
  };

  onChangeShowCompleted = () => {
    this.setState(prevState => {
      return {
        ...prevState,
        showCompleted: !prevState.showCompleted
      };
    });
  };

  onAddTodo = newTitle => {
    const newTodo = {
      title: newTitle,
      isCompleted: false
    }
       
    axios.post('https://boiling-woodland-05459.herokuapp.com/api/', newTodo)
    .then(res => {
      this.onReadTodos();
      // let todos = this.state.todos.slice();
      // todos.push(res);
      // this.setState(prevState => {
      //   return {
      //     ...prevState,
      //     todos: todos
      //   };
      // });
    })
  };

  render() {
    let todoList;
    if (this.state.todos.length === 0) {
      todoList = <p>Загрузка...</p>;
    } else {
      todoList = (
        <>
          {this.state.todos.map(item => {
            if (!this.state.showCompleted && item.isCompleted) return;
            else
              return (
                <Todo
                  key={item.id}
                  todo={item}
                  showCompleted={this.state.showCompleted}
                  onChangeTodoCompleted={this.onChangeTodoCompleted}
                />
              );
          })}

          <ListGroup.Item>
            <AddTodo onAddTodo={this.onAddTodo}/>
          </ListGroup.Item>

          <ListGroup.Item>
            <TodoSettings
              showCompleted={this.state.showCompleted}
              onChangeShowCompleted={this.onChangeShowCompleted}
            />
          </ListGroup.Item>
        </>
      );
    }

    return (
      <div className="App">
        <header className="App-header">{todoList}</header>
      </div>
    );
  }
}

export default App;
